// colors
[data-dark="false"] {
  --primary: #003366;      /* 深蓝，学术感强 */
  --secondary: #4a6fa5;    /* 次要蓝灰色 */
  --text: #1a1a1a;         /* 深灰，减少强对比 */
  --background: #ffffff;   /* 白色背景 */
  --background-alt: #f5f5f5; /* 论文风格的灰底 */
  --light-gray: #d9d9d9;
  --gray: #7a7a7a;
  --dark-gray: #3a3a3a;
  --overlay: #00000020;
}
[data-dark="true"] {
  --primary: #4a90e2;      /* 夜间深蓝 */
  --secondary: #2c3e50;    /* 深灰蓝，稳重 */
  --text: #eaeaea;         /* 柔和白字 */
  --background: #121212;   /* 深色背景 */
  --background-alt: #1c1c1c;
  --light-gray: #3a3a3a;
  --gray: #8c8c8c;
  --dark-gray: #c0c0c0;
  --overlay: #ffffff10;
}

:root {
  // font families
  --title: "Merriweather", serif;     /* 学术感强的衬线标题 */
  --heading: "Lora", serif;           /* 小标题保持稳重 */
  --body: "Inter", sans-serif;        /* 正文清晰现代 */
  --code: "Roboto Mono", monospace;

  // font sizes
  --large: 1.1rem;
  --xl: 1.3rem;
  --xxl: 1.5rem;

  // font weights
  --thin: 300;
  --regular: 400;
  --semi-bold: 600;
  --bold: 700;

  // text line spacing
  --spacing: 1.8;   /* 稍紧凑，保持专业 */
  --compact: 1.4;

  // effects
  --rounded: 2px;   /* 小圆角，更严肃 */
  --shadow: 0 0 6px 0 var(--overlay); /* 更轻的阴影 */
  --transition: 0.15s ease;           /* 快速过渡 */
}
